.nivel_principal{
    background-color: var(--darkreader-background);
    padding: 15px 0;
    text-align: center;
}
.nivel_principal > h2, .nivel_principal > h3{
    font-size: 3.5rem;
    color: rgb(10, 200, 238);
    text-align: center;
    width: -webkit-max-content;
    width: max-content;
    margin: 20px auto;
}
.nivel_principal > h3{
    color: var(--darkreader-text-gray);
    font-size: 2.5rem;
    -webkit-font-feature-settings: "smcp";
            font-feature-settings: "smcp";
    font-variant: small-caps;
}
.dumbbell{
    font-size: 2rem;
    rotate: -40deg;
    margin: 0 10px;
    color: #ff7844;
}
.dumbbell:nth-child(2){
    rotate: 40deg;
}
.nivelContainer{
    background-color: #17191a;
    width: 97%;
    margin: 10px auto;
    border-radius: 20px;
    overflow: hidden;
    text-align: center;
    color: var(--darkreader-text-gray);
}
.nivelContainer .datos{
    width: 95%;
    max-width: 1400px;
    margin: 10px auto;
    border-radius: 20px;
    display: flex;
}
.imagen{
    display: block;
    width: 30%;
    max-width: 400px;
    max-height: 300px;
    margin: auto;
}
.imagen > img{
    max-width: 100%;
    max-height: 300px;
    object-fit: cover;
    border: solid 1px black;
    border-radius: 20px;
}
.nivelSlider .containerSliderPrincipal .sliderPrincipal .sliderSection img{
    height: auto;
    max-height: 300px;
    object-fit: contain;
    border: solid 1px black;
    border-radius: 20px;
    background-color: rgb(24, 24, 26);
    pointer-events: all;
}
.nivelSlider .containerSliderPrincipal .sliderPrincipal{
    background-color: transparent;
    height: 300px;
    max-height: none;
    display: flex;
    justify-content: center;
    align-items: center;
}
.nivelSlider .containerSliderPrincipal{
    height: 300px;
}
.sliderNotFound > h5{
    font-size: 1.5rem;
    width: 90%;
    margin: auto;
}
.info{
    padding: 15px 0;
    margin: auto;
    margin-right: 0;
    width: 70%;
}
.info > p, .info > h4{
    width: 90%;
    max-width: 840px;
    margin: 0 auto;
}
.info > p{
    text-align: left;
    font-size: 1.5rem;
}
.info > h5{
    font-size: 2rem;
}
.info > a{
    text-decoration: none;
    font-size: 2rem;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    background: #ff7844;
    background: linear-gradient(45deg, #d93f87, #ff7844, #ffdd55, #ff7844, #d93f87, #ff7844, #ffdd55, #ff7844);
    background-size: 800%;
    color: transparent;
    -webkit-background-clip: text;
            background-clip: text;
    transition: all 0.3s ease;
}
.info >a:hover{
    -webkit-animation: magicBtn 15s linear infinite;
            animation: magicBtn 15s linear infinite;
}
@-webkit-keyframes magicBtn{
    0%{
        background-position: 0% 0;
    }
    50%{
        background-position: 400% 0;
    }
    100%{
        background-position: 0% 0;
    }
}
@keyframes magicBtn{
    0%{
        background-position: 0% 0;
    }
    50%{
        background-position: 400% 0;
    }
    100%{
        background-position: 0% 0;
    }
}
.b_niveles{
    padding: 10px 20px;
    -webkit-font-feature-settings: "smcp";
            font-feature-settings: "smcp";
    font-variant: small-caps;
    line-height: 100px;
}
@media screen and (max-width: 950px) {
    .nivelContainer .datos{
        display: block;
    }
    .imagen{
        width: 100%;
    }
    .info{
        width: 100%;
    }
}
.slider{
    height: 100vh;
    width: 100%;
}
.containerSliderPrincipal{
    -webkit-user-select: none;
            user-select: none;
    width: 100%;
    height: 50vw;
    max-height: 100vh;
    overflow: hidden;
    margin: auto;
    position: relative;
}
.imgSliderPrincipal{
    pointer-events: none;
    width: 100%;
    height: 50vw;
    max-height: 100vh;
    object-fit: cover;
}
.sliderPrincipal{
    background-color: rgb(24, 24, 26);
    height: 50vw;
    max-height: 100vh;
}
.sliderSection{
    display: none;
}
.carrouselActive{
    display: block;
    visibility: visible;
    -webkit-animation: imgScale linear .2s forwards;
            animation: imgScale linear .2s forwards;
}
@-webkit-keyframes imgScale {
    0%{opacity: .5;}
    
    100%{opacity: 1;}
}
@keyframes imgScale {
    0%{opacity: .5;}
    
    100%{opacity: 1;}
}

.containerBtn{
    position: absolute;
    top: 0;
    cursor: pointer;
    place-items: center;
    display: grid;
    height: 100%;
    width: 30%;
}
.containerBtn .arrowIcon{
    Color: rgba(255, 255, 255, 0.7);
    background-color: transparent;
    font-size: 4rem;
    font-family: monospace;
    border-radius: 50%;
}
.containerBtn:hover .arrowIcon{
    background: rgba(0, 0, 0, .5);
    -webkit-backdrop-filter: blur(2px);
            backdrop-filter: blur(2px);
    Color: rgba(255, 255, 255);
}
.sliderbtnLeft{
    left: 0;
}
.sliderbtnRight{
    right: 0;
}
.sliderbtnLeft .arrowIcon{
    position: absolute;
    left: 10px;
}
.sliderbtnRight .arrowIcon{
    position: absolute;
    right: 10px;
}
@media screen and (max-width: 500px) {
    .containerBtn p{
        font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    }
}
